import React from 'react';
import { Link } from "gatsby"


const StudioCard = (props) =>
{

    const { link, img1, img2 } = props.data
    const image1 = require(`../../images/studio/${img1}.png`)
    const image2 = require(`../../images/studio/${img2}.png`)




    return (
        <Link className="studio-anchor" to={`/studio/year-${link}`}>
            <div className="studio-cards-main">
                <div className="studio-cards"></div>
                <img className="hover-img" src={image1} alt="" />
                <img className="hover-img2" src={image2} alt="" />
                <span>{link}</span>
            </div>
        </Link>
    );
}


export default StudioCard