import React from 'react'
import cardData from "./studioData"
import StudioCard from "./studioCard"

const image = require("../../images/studio/snake-normal.png")

const StudyYears = (props) => {

    return (
        <div className="flex-between studio-1 container">
            <div className="absImg absImg1"></div>
            <div className="absImg absImg2"></div>
            <div className="absImg absImg3"></div>
            <div className="absImg absImg4"></div>
            {cardData.map((item, index) => (<StudioCard data={item} key={index} />))}
        </div>
    );
}

export default StudyYears;